<template>
  <div>
    <table class="table">
      <tr>
        <th>아이디</th>
        <td><input type="text" id="qnaTitle" v-model="qnaTitle" /></td>
      </tr>
      <tr>
        <th>질문내용</th>
        <td><input type="text" id="qnaContent" v-model="qnaContent" /></td>
      </tr>
    </table>

    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">수정</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Update',
  data() {
    return {
      qnaTitle: '',
      qnaContent: '',
      qnaNo: '',
      qnaUserid: '',
    };
  },
  methods: {
    moveList() {
      this.$router.push('/list');
    },
    checkHandler() {
      let err = true;
      let msg = '';
      !this.qnaTitle && ((msg = '제목을 입력해주세요'), (err = false));
      err && !this.qnaContent && ((msg = '내용을 입력해주세요'), (err = false));
      if (!err) alert(msg);
      else this.updateHandler();
    },
    updateHandler() {
      //   const params = new URL(document.location).searchParams;

      axios
        .put(`http://localhost:9999/happyhouse/api/qna/${this.qnaNo}`, {
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaNo: this.qnaNo,
          qnaUserid: this.qnaUserid,
        })
        .then((response) => {
          if (response.data === 'success') {
            alert('수정이 완료되었습니다.');
          } else {
            alert('수정처리시 문제가 발생하였습니다.');
          }
        })
        .catch((error) => {
          this.error = error;
        })
        .finally(() => {
          this.$router.push('/list');
        });
    },
  },
  created() {
    const params = new URL(document.location).searchParams;
    axios;
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get('qnaNo')}`)
      .then(({ data }) => {
        console.dir(data);
        this.qnaTitle = data.qnaTitle;
        this.qnaContent = data.qnaContent;
        this.qnaNo = data.qnaNo;
        this.qnaUserid = data.qnaUserid;
      })
      .catch((error) => {
        console.dir(error);
      });
  },
};
</script>

<style></style>
