<template>
  <div>
    <table class="table table-bordered w-50">
      <tr>
        <th>질문번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>질문일시</th>
        <td>{{ item.qnaDatetime }}</td>
      </tr>
      <tr>
        <th>질문제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>질문내용</th>
        <td>{{ item.qnaContent }}</td>
      </tr>
      <tr>
        <th>질문자아이디</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
    </table>

    <hr />
    <table v-if="item.replyUserid">
      <tr>
        <th>답변내용</th>
        <td>{{ item.manager_id }}</td>
      </tr>
      <tr>
        <th>답변일시</th>
        <td>{{ item.title }}</td>
      </tr>
      <tr>
        <th>답변자아이디</th>
        <td>{{ item.dept_id }}</td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <router-link to="/list" class="btn btn-primary">목록</router-link>
      <router-link :to="'/update?qnaNo=' + item.qnaNo" class="btn btn-primary">수정</router-link>
      <router-link :to="'/delete?qnaNo=' + item.qnaNo" class="btn btn-primary">삭제</router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
export default {
  name: 'Detail',
  data() {
    return {
      item: {},
      error: '',
    };
  },
  created() {
    //http://localhost:9999/vue/api/board/2
    const params = new URL(document.location).searchParams;
    axios
      .get(`http://localhost:9999/happyhouse/api/qna/${params.get('qnaNo')}`)
      .then(({ data }) => {
        // response내부의 data변수를 사용할떄 destructuring
        console.dir(data);
        this.item = data;
      })
      .catch((error) => {
        console.dir(error);
        this.error = error;
      });
  },
  methods: {
    getFormatDate(regtime) {
      // moment 라이브러리를 이용하여 날짜 정보를 년.월.일 시:분:초 의 형태로 반환한다.
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>

