<template>
  <div>
    <table class="table">
      <tr>
        <th>질문제목</th>
        <td><input type="text" id="qnaTitle" v-model="qnaTitle" /></td>
      </tr>
      <tr>
        <th>질문내용</th>
        <td><input type="text" id="qnaContent" v-model="qnaContent" /></td>
      </tr>
      <tr>
        <th>아이디</th>
        <td><input type="text" id="qnaUserid" v-model="qnaUserid" /></td>
      </tr>
    </table>

    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">등록</button>
      <button class="btn btn-primary" @click="moveList">목록</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'Insert',
  data() {
    // data는 가져올떄 함수형(return으로 가져와야한다.)
    // 목록 데이터 저장을 위한 배열 선언(배열의 이름 : items)
    return {
      qnaContent: '',
      qnaTitle: '',
      qnaUserid: '',
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = '';
      !this.qnaTitle && ((msg = '제목을 입력해주세요'), (err = false));
      err && !this.qnaContent && ((msg = '내용을 입력해주세요'), (err = false));
      err && !this.qnaUserid && ((msg = '아이디를 입력해주세요'), (err = false));
      if (!err) alert(msg);
      else {
        this.createHandler();
      }
    },
    moveList() {
      this.$router.push('/list');
    },
    createHandler() {
      axios
        .post('http://localhost:9999/happyhouse/api/qna', {
          qnaContent: this.qnaContent,
          qnaTitle: this.qnaTitle,
          qnaUserid: this.qnaUserid,
        })
        .then((response) => {
          if (response.data === 'success') {
            alert('등록이 완료되었습니다.');
          } else {
            alert('등록 처리시 문제가 발생했습니다.');
          }
          this.moveList();
        })
        .catch((error) => {
          this.error = error;
        });
    },
  },
};
</script>

