<template>
  <div>
    <div>
      <input type="text" id="title" @keyup.enter="findBoard" v-model="title" />
      <button class="btn btn-primary" @click="findBoard">검색</button>
    </div>
    <div v-if="items.length">
      <table class="table table-bordered table-condensed">
        <colgroup>
          <col :style="{ width: '10%' }" />
          <col :style="{ width: '50%' }" />
          <col :style="{ width: '15%' }" />
          <col :style="{ width: '25%' }" />
        </colgroup>
        <tr>
          <th>질문번호</th>
          <th>질문제목</th>
          <th>질문내용</th>
          <th>질문자아이디</th>
          <th>질문일시</th>
        </tr>
        <tr v-for="(item, index) in items" :key="index">
          <td>{{ item.qnaNo }}</td>

          <td>
            <router-link :to="'/detail?qnaNo=' + item.qnaNo">{{ item.qnaTitle }}</router-link>
          </td>
          <td>{{ item.qnaContent }}</td>
          <td>{{ item.qnaUserid }}</td>
          <td>{{ item.qnaCatetime }}</td>
        </tr>
      </table>
    </div>
    <div v-else class="text-center">등록 질문이 없습니다.</div>
    <div class="text-right">
      <button class="btn btn-primary" @click="movePage">질문 등록</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
export default {
  name: 'List',
  data() {
    return {
      items: [],
      title: '',
    };
  },
  // created 라이프사이클 훅 선언
  created() {
    axios
      .get('http://localhost:9999/happyhouse/api/qna')
      .then((response) => {
        this.items = response.data;
        console.dir(this.items);
        console.log(this.items.length);
      })
      .catch((error) => {
        console.dir(error);
      });
  },
  // 메서드들을 선언한다.
  methods: {
    // 목록 데이터 반복시 날짜 정보 문자열을 받아 원하는 형태로 변환하는 함수를 정의한다.
    // 함수이름 : getFormatDate
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD');
    },
    // 등록 페이지로 이동할 movePage 메서드 정의
    movePage() {
      // create.html 로 호출한다.
      this.$router.push('/insert');
    },
    findBoard() {
      let newBoards = [];
      if (this.title) {
        if (this.items.length == 0) {
          alert('질문게시글이 없습니다.');
        } else {
          for (let item of this.items) {
            if (item.qnaTitle.includes(this.title)) newBoards.push(item);
          }
          //this.empls = [];
          this.items = newBoards;
        }
      } else {
        alert('검색할 게시글명 을 입력하세요');
      }
    },
  },
};
</script>

